<template>
<div class="w-screen h-screen">
	<div id="container"></div>
</div>
</template>

<script>
import * as THREE from 'three';
export default {
	data() {
		return {
			camera: null,
			container: null,
			sence: null,
			renderer: null,
			isUserInteracting : false,
			onPointerDownMouseX : 0, 
			onPointerDownMouseY : 0,
			lon : 0, 
			onPointerDownLon : 0,
			lat : 0, 
			onPointerDownLat : 0,
			phi : 0, 
			theta : 0
		}
	},
	methods: {
		init() {
      this.container = document.getElementById("container");
      this.camera = new THREE.PerspectiveCamera(
        75,
        window.innerWidth / window.innerHeight,
        1,
        1100
      );
      this.scene = new THREE.Scene();
      const geometry = new THREE.SphereBufferGeometry(500, 60, 40);
      // invert the geometry on the x-axis so that all of the faces point inward
      geometry.scale(-1, 1, 1);
      const texture = new THREE.TextureLoader().load("/panoramas/insideV.jpg");
      const material = new THREE.MeshBasicMaterial({ map: texture });
      const mesh = new THREE.Mesh(geometry, material);
      this.scene.add(mesh);
      this.renderer = new THREE.WebGLRenderer();
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      this.container.appendChild(this.renderer.domElement);
      this.container.style.touchAction = "none";
      this.container.addEventListener("pointerdown", this.onPointerDown, false);
      document.addEventListener("wheel", this.onDocumentMouseWheel, false);
      //
      document.addEventListener(
        "dragover",
        function (event) {
          event.preventDefault();
          event.dataTransfer.dropEffect = "copy";
        },
        false
      );
      document.addEventListener(
        "dragenter",
        function () {
          document.body.style.opacity = 0.5;
        },
        false
      );
      document.addEventListener(
        "dragleave",
        function () {
          document.body.style.opacity = 1;
        },
        false
      );
      document.addEventListener(
        "drop",
        function (event) {
          event.preventDefault();
          const reader = new FileReader();
          reader.addEventListener(
            "load",
            function (event) {
              material.map.image.src = event.target.result;
              material.map.needsUpdate = true;
            },
            false
          );
          reader.readAsDataURL(event.dataTransfer.files[0]);
          document.body.style.opacity = 1;
        },
        false
      );
      // Helper
      {
        const helper = new THREE.AxesHelper(10);
        this.scene.add(helper);
      }
      //
      window.addEventListener("resize", this.onWindowResize, false);
      this.animate();
    },
    onWindowResize() {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(window.innerWidth, window.innerHeight);
    },
    onPointerDown(event) {
      if (event.isPrimary === false) return;
      this.isUserInteracting = true;
      this.onPointerDownMouseX = event.clientX;
      this.onPointerDownMouseY = event.clientY;
      this.onPointerDownLon = this.lon;
      this.onPointerDownLat = this.lat;
      document.addEventListener("pointermove", this.onPointerMove, false);
      document.addEventListener("pointerup", this.onPointerUp, false);
    },
    onPointerMove(event) {
      if (event.isPrimary === false) return;
      this.lon =
        (this.onPointerDownMouseX - event.clientX) * 0.1 +
        this.onPointerDownLon;
      this.lat =
        (event.clientY - this.onPointerDownMouseY) * 0.1 +
        this.onPointerDownLat;
    },
    onPointerUp() {
      if (event.isPrimary === false) return;
      this.isUserInteracting = false;
      document.removeEventListener("pointermove", this.onPointerMove);
      document.removeEventListener("pointerup", this.onPointerUp);
    },
    // onDocumentMouseWheel( event ) {
    // 	const fov = this.camera.fov + event.deltaY * 0.05;
    // 	this.camera.fov = THREE.MathUtils.clamp( fov, 10, 75 );
    // 	this.camera.updateProjectionMatrix();
    // },
    animate() {
      requestAnimationFrame(this.animate);
      this.update();
    },
    update() {
      if (this.isUserInteracting === false) {
        this.lon += 0.08;
      }
      this.lat = Math.max(-85, Math.min(85, this.lat));
      this.phi = THREE.MathUtils.degToRad(90 - this.lat);
      this.theta = THREE.MathUtils.degToRad(this.lon);
      const x = 500 * Math.sin(this.phi) * Math.cos(this.theta);
      const y = 500 * Math.cos(this.phi);
      const z = 500 * Math.sin(this.phi) * Math.sin(this.theta);
      this.camera.lookAt(x, y, z);
      this.renderer.render(this.scene, this.camera);
    },
	},
	mounted() {
		this.init();
		window.addEventListener('resize', this.onWindowResize, false);
	},
}
</script>
<style>
	.background{
		color: #999999;
		top: 0;
		left: 0;
		position: absolute;
		z-index: -100;
	}
</style>